.new-arrivals {
  background-color: var(--black-background-color);
  padding-top: 80px;
  padding-bottom: 80px;

  ::-webkit-scrollbar {
    height: 2px;
  }

  ::-webkit-scrollbar-track {
    background: var(--dark-gray-background-color);
    border-radius: 8px;
  }

  ::-webkit-scrollbar-thumb {
    background-color: var(--white-base-color);
    border-radius: 8px;
  }

  @media (min-width: 1440px) {
    padding-top: 120px;
    padding-bottom: 150px;
  }
}

.new-arrivals__title {
  position: relative;

  text-align: center;
  font-family: "Playfair Display";
  font-size: 26px;
  font-weight: 700;
  line-height: normal;
  text-transform: uppercase;

  margin-bottom: 50px;


  @media (min-width: 768px) {
    font-size: 36px;
    margin-bottom: 80px;
  }

  @media (min-width: 1440px) {
    margin-bottom: 121px;

    &::after {
      content: '';

      position: absolute;
      bottom: -40px;
      left: 50%;

      width: 120px;
      height: 1px;

      background-color: var(--white-base-color);
      transform: translateX(-50%);
    }
  }
}

.clocks-list__wrapper {
  overflow-x: hidden;

  @media (min-width: 1024px) {
    overflow-x: visible;
  }
}


.clocks-list {
  display: flex;
  margin-left: -228px;

  position: relative;
  left: 0;

  transition: all 300ms linear;


  @media (min-width: 768px) {
    margin-left: 0px;
  }

  @media (min-width: 1024px) {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(260px, 1fr));
    column-gap: 23px;
    row-gap: 40px;
  }
}

.clocks-list__item {
  padding-right: 20px;

  @media (min-width: 768px) {
    &:first-child {
      display: none;
    }
  }

  @media (min-width: 1024px) {
    padding-right: 0px;
  }
}


.clocks-list__link {
  display: block;
  max-width: 260px;
  padding: 27px 22px 34px;
  margin-bottom: 25px;

  background: #191B1C;

  @media (min-width: 1024px) {
    max-width: 100%;
  }
}

.clocks-list__text {
  font-family: "Playfair Display";
  font-size: 18px;
  line-height: normal;

  margin-bottom: 10px;
}

.clocks-list__price {
  color: #D0D0D0;
  font-family: "Playfair Display";
  font-size: 16px;
  font-weight: 700;
  line-height: normal;
}

.clocks-list__img {
  width: 215px;
  height: 264px;

  @media (min-width: 768px) {
    width: 194px;
    height: auto;
  }

  @media (min-width: 1024px) {
    width: 100%;
    margin: 0 auto;
  }
}

.clocks-list__item_blur {
  position: relative;
}

.clocks-list__img_blur {
  filter: blur(2px);
}

.clocks-list__text_blur {
  position: absolute;
  top: 135px;
  left: 50%;

  transform: translateX(-55%);

  text-align: center;
  font-family: "Playfair Display";
  font-size: 18px;

  font-weight: 400;
  line-height: normal;

  padding: 12px 28px;

  border: 1px solid #FFF;

  width: 190px;

  @media (min-width: 1024px) {
    top: 175px;
    transform: translateX(-50%);
  }

  @media (min-width: 1440px) {
    top: 138px;
  }
}

.clocks-list__btn-box {
  display: flex;
  justify-content: space-between;

  margin-top: 30px;
  padding: 0 10px;

  @media (min-width: 768px) {
    justify-content: center;
    margin-top: 35px;
  }

  @media (min-width: 1024px) {
    display: none;
  }
}

.btn-box {
  display: flex;
  column-gap: 20px;

  &>button {
    display: flex;
    align-items: center;
    color: (var(--white-base-color));
    text-align: center;
    font-family: "PT Sans";
    font-size: 28px;

    line-height: 1;

    background-color: transparent;
    border: 1px solid var(--white-base-color);

    padding: 3px 18px 10px;

    @media (min-width: 768px) {
      padding: 3px 28px 10px;
    }
  }

  &__count {
    font-family: 'Playfair Display';
    font-size: 38px;
    line-height: 0.8;

    &>span:nth-child(2) {
      display: inline-block;
      font-size: 35px;

      padding: 0px 5px;
    }

    @media (min-width: 768px) {
      display: none;
    }
  }

  @media (min-width: 768px) {
    column-gap: 35px;
  }
}