*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

:root {
  --primary-font: "PT Sans", sans-serif;
  --secondary-font: "Playfair Display", serif;
  --white-base-color: #fff;
  --gray-base-color: #969696;
  --light-gray: #d0d0d0;
  --dark-gray-background-color: #0f0f0f;
  --black-background-color: #000;
}

html {
  scroll-behavior: smooth;
}

a {
  color: var(--white-base-color);
  text-decoration: none;
}

ul, ol, li, menu {
  margin: 0;
  padding: 0;
  list-style: none;
}

img {
  width: 100%;
  display: block;
}

button {
  cursor: pointer;
  border: none;
  padding: 0;
  line-height: 0;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

@font-face {
  font-family: Post No Bills Jaffna;
  src: url("PostNoBillsJaffna-Bold.2fb07ea6.woff");
}

.wrapper {
  max-width: 1920px;
  margin: 0 auto;
}

@media (min-width: 768px) {
  .wrapper {
    overflow: hidden;
  }
}

body {
  font-family: var(--primary-font);
  color: var(--white-base-color);
  background-color: var(--dark-gray-background-color);
  font-size: 14px;
  line-height: 150%;
}

.to-freeze {
  overflow: hidden;
}

.cont-r {
  margin: 0 auto;
  padding: 0 5px;
}

@media (min-width: 375px) {
  .cont-r {
    max-width: 370px;
  }
}

@media (min-width: 768px) {
  .cont-r {
    max-width: 760px;
  }
}

@media (min-width: 1024px) {
  .cont-r {
    max-width: 1020px;
  }
}

@media (min-width: 1440px) {
  .cont-r {
    max-width: 1120px;
  }
}

.mob-menu {
  width: 100vw;
  height: 100dvh;
  z-index: 99;
  opacity: 0;
  padding: 52px 10px 20px 20px;
  transition: all .3s ease-in-out;
  position: fixed;
  top: 0;
  right: 0;
  transform: translateX(100%);
}

.mob-menu_flex {
  background: var(--dark-gray-background-color);
  opacity: .95;
}

@media (min-width: 500px) {
  .mob-menu_flex {
    width: 60vw;
  }
}

@media (min-width: 600px) {
  .mob-menu_flex {
    width: 50vw;
  }
}

.mob-menu-list__wrapper {
  height: calc(100dvh - 68px);
  flex-direction: column;
  display: flex;
  overflow-y: auto;
}

.mob-menu__btn {
  padding: 0;
  line-height: 0;
}

.mob-menu_container {
  position: relative;
}

.mob-menu__btn_close {
  color: #212121;
  cursor: pointer;
  background-color: #0000;
  border: none;
  justify-content: center;
  align-items: center;
  padding: 8px;
  display: flex;
  position: absolute;
  top: 8px;
  right: 8px;
}

.mob-menu-list__item:first-child {
  margin-top: 32px;
}

.mob-menu-list__item {
  margin-bottom: 32px;
}

.mob-menu-list__link {
  text-transform: uppercase;
  font-size: 25px;
  line-height: 125%;
}

.desc {
  column-gap: 16px;
  margin-top: auto;
  padding-bottom: 10px;
  display: flex;
}

.desc__text {
  color: #969696;
  font-family: PT Sans;
  line-height: normal;
}

.mob-menu.is-hidden {
  opacity: .99;
  transform: translateX(0%);
}

.backdrop {
  width: 100%;
  height: 110%;
  z-index: 2;
  backdrop-filter: blur(5px);
  background: #140b01ce;
  transition-property: opacity, visibility;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
}

.backdrop--hidden {
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
}

@media (min-width: 1024px) {
  .accordion {
    display: none;
  }
}

.box {
  background-color: #514f4fd8;
  border-radius: 4px;
  margin-bottom: 12px;
  position: relative;
  overflow: hidden;
}

.box:last-child {
  margin-bottom: 0;
}

.label {
  letter-spacing: -.8px;
  letter-spacing: 1px;
  text-transform: uppercase;
  cursor: pointer;
  padding: 16px;
  font-family: Playfair Display;
  font-size: 18px;
  line-height: normal;
  position: relative;
}

.label > svg {
  pointer-events: none;
  transform-origin: center;
  width: 30px;
  transition: all .3s ease-in-out;
  position: absolute;
  top: 35%;
  right: 16px;
}

@media (min-width: 768px) {
  .label > svg {
    top: 30%;
  }
}

@media (min-width: 1440px) {
  .label {
    font-size: 24px;
  }
}

.content {
  max-height: 0;
  font-weight: 400;
  line-height: 125%;
  transition: all .3s ease-in-out;
  position: relative;
  overflow: hidden;
}

.content__inner {
  border: 2px solid #514f4fd8;
  border-top: none;
  padding: 16px;
}

.content__inner_links {
  flex-wrap: wrap;
  align-items: center;
  gap: 10px 6px;
  display: flex;
}

@media (min-width: 768px) {
  .content__inner_links {
    column-gap: 10px;
  }
}

.content__link {
  text-align: center;
  color: var(--light-gray);
  border: 1px solid #514f4fd8;
  padding: 10px 20px;
  font-size: 14px;
  line-height: 150%;
  display: block;
}

@media (min-width: 768px) {
  .content__link {
    flex-grow: 1;
  }
}

.content__form {
  max-width: 350px;
  outline: 1px solid #fff6;
  margin-top: 20px;
  display: flex;
}

@media (min-width: 768px) {
  .content__form {
    margin-left: auto;
  }
}

.content__input {
  width: 100%;
  height: 42px;
  background: #fff6;
  border: none;
  outline: none;
  padding: 10px;
}

.content__input::placeholder {
  color: var(--white-base-color);
  letter-spacing: .7px;
}

.content__button {
  color: var(--white-base-color);
  text-align: center;
  letter-spacing: .7px;
  text-transform: uppercase;
  background: #0f0f0f;
  padding: 10px 20px;
  line-height: 150%;
}

.active > .label {
  color: var(--white-base-color);
  background: #514f4fd8;
}

.active > .label > svg {
  transform: rotate(180deg);
}

.active > .label ~ .content {
  color: var(--white-base-color);
  background: var(--dark-gray-background-color);
  font-weight: 400;
  line-height: 125%;
}

@media (max-width: 767px) {
  .header {
    z-index: 10;
    position: sticky;
    top: 0;
    left: 0;
  }
}

.header__wrapper-top {
  background-color: var(--black-background-color);
  padding: 9px 0;
}

@media (min-width: 768px) {
  .header__wrapper-top {
    padding: 14px 0;
  }
}

.registration-box__icon {
  width: 14px;
  height: 14px;
}

.header__container {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.top-list {
  align-items: center;
