}

.header-bottom__btn-box > button {
  background-color: #0000;
}

@media (min-width: 768px) {
  .header-bottom__btn-box {
    column-gap: 40px;
  }
}

@media (min-width: 768px) {
  .header-bottom__btn-burger {
    display: none;
  }
}

